class createprothree extends Interstellar.pagesBase {
    complete() {
        let that = this;
        this.hideBtn = window.location.hash.indexOf('createprothree') !== -1
        this.projectType = window.location.hash.indexOf('createprothree') !== -1 ? 1 : 3
        if (this.projectType == 1) {
            this.app.header.openControl('projectmanage')
        } else {
            this.app.header.openControl('backflowproject')
        }
        if(this.hideBtn == true){
            this.dom.find('.isgetalgres').addClass('hide');
            this.dom.find('.copd').addClass('hide');
            this.dom.find('.fjj').addClass('hide');
        }else{
            this.dom.find('.isgetalgres').removeClass('hide');
            this.dom.find('.copd').removeClass('hide');
            this.dom.find('.fjj').removeClass('hide');
        }
        this.app.header.changeselected(3)
        this.styleModel(1)
        this.resize()
        this.type = this.app.parpam.type
        this.projectId = parseInt(this.app.parpam.projectid)
        this.render()
        this.apiData = {
            seriesInstanceUidList: [],
            condition: {},
            projectId: parseInt(this.projectId)
        }
      this.get_last_import_log()
      this.alg_sar_list_tool()
      this.heartSegment_resultRead()
        this.viewData = { type: this.projectType }
        this.component = {}
        this.prodata = require("../modal/projectdata/projectdata")
        let addimage = require("../modal/addimage/addimage")
        this.uploadalgdata = require("../modal/uploadalgdata/uploadalgdata")
        this.alglist = require("../modal/alglist/alglist")
      this.formsubmit = require("../modal/formsubmit/formsubmit")
        if (this.type != 'new' && !this.projectId) {
            this.app.changePage('projectmanage')
            return
        }
        if (this.type == 'new') {
            this.app.changePage('projectmanage')
            return
        }
        if (this.type == 'view') {
            this.dom.find('.biaozhubtn').addClass('hide');
            this.dom.find('.yyytask').removeClass('hide');
            this.setTitle();
        }
        if (this.type == 'edit') {
            if (!this.projectId) {
                this.app.changePage('projectmanage')
            }
        }
        //数据调用
        this.model._event._addEvent('totalInfo.change', () => {
            if (this.model.getData('hospitalInfo').length > 0 && this.model.getData('listInfo').list) {
                let timestamp = new Date().getTime();
                this.addmodel = this.app.loadModal(addimage, { adv: true }, {
                    data: this.model.getData('listInfo'),
                    hospital: this.model.getData('hospitalInfo'),
                    modality: this.model.getData('modalityInfo')
                })
                this.addmodel.event._addEvent('addimage.search', (value) => {
                    this.getlist(value.data);
                })
                this.addmodel.event._addEvent('hospital.input', (value) => {
                    this.gethospital(value.data.data);
                    this.model._event._addEvent('hospitalInfo.change', () => {
                        this.addmodel.setxlk(this.model.getData('hospitalInfo'))
                    })
                })
                this.addmodel.event._addEvent('addimage.all', (value) => {
                    if (value.data !== undefined) {
                        this.apiData.condition = value.data;
                        delete this.apiData.condition.page;
                        delete this.apiData.condition.pageSize;
                    } else {
                        this.apiData.condition = {};
                    }
                    this.addimage(1)
                    this.addmodel.close()
                    this.addmodel = null
                })
                this.addmodel.event._addEvent('addimage.new', (value) => {
                    if (value.data !== undefined) {
                        this.apiData.condition = value.data;
                        delete this.apiData.condition.page;
                        delete this.apiData.condition.pageSize;
                    } else {
                        this.apiData.condition = {};
                    }
                    this.clearandnew()
                })
            }
        })
        this.model._event._addEvent('listInfo.change', () => {
            this.addmodel.setMain(true, this.model.getData('listInfo'))
        })
        this.dom.find('.addimage1').on('click', () => {
            this.model.hospitalInfo = []
            this.model.listInfo = []
            this.gethospital('', true);
            this.getlist({
                category: 'RADIOLOGY',
                valid: true,
                jpgValid:true,
                page: 1,
                pageSize: 10
            }, true);
        })
        this.dom.find('.viewimage').on('click', () => {
            let temphtml = '<a class="biaozhubtn deleteres">删除查询结果</a>\n' +
                '            <a class="biaozhubtn exportname">导出结果序列名</a>'
            this.prodatapoll = this.app.loadModal(this.prodata, { adv: true }, { type: 'custom', html: temphtml, del: this.type })
            this.gethospital('')
            this.prodatapoll.event._addEvent('prodata.pagenumber', (value) => {
                this.resetPage = false;
                this.getseries(value.data);
            })
            this.prodatapoll.event._addEvent('hospital.input', (value) => {
                this.gethospital(value)
            })
            this.prodatapoll.event._addEvent('projectdata.taskinput', (value) => {
                this.gettask(value)
            })
            this.prodatapoll.event._addEvent('projectdata.search', (value) => {
                this.resetPage = true
                this.getseries(value.data);
            })
            this.prodatapoll.event._addEvent('prodata.pagesize', (value) => {
                this.resetPage = true;
                this.getseries(value.data);
            })
            this.prodatapoll.event._addEvent('projectdata.deleteres', (value) => {
                this.app.alert.show({
                    title: ' ',
                    msg: '如有已关联到任务的序列不支持删除',
                    close: true,
                    sure: async () => {
                        this.app.loading.show()
                        delete value.condition.page;
                        delete value.condition.pageSize;
                        let json = {
                            condition: value.condition,
                            operation: 'ID',
                            projectId: this.projectId,
                            currentSearchReqId: value.currentSearchReqId
                        }
                        let res = await this.api.searchresult_remove(json)
                        this.app.loading.hide()
                        if (res.code == 0) {
                            this.series_count()
                            this.prodatapoll.close()
                            this.prodatapoll = null
                        }
                    }
                })
            })
            this.prodatapoll.event._addEvent('projectdata.export', (value) => {
                let json = {
                    condition: value.condition,
                    operation: 'ID',
                    projectId: this.projectId,
                    currentSearchReqId: value.currentSearchReqId
                }
                let url = this.app.domain1 + 'v1/project/series/searchresult/export?param=' + encodeURI(JSON.stringify(json) + '&accessToken=' + window.localStorage.accessToken);
                //let token = this.app.local.get('accessToken')
                this.api.HttpRequest.downLoadFile(url, { "key": "accessToken", 'val': this.app.local.get('accessToken') })
            })
            this.resetPage = true;
            this.getseries();
        })
        this.dom.find('.addsfdata').on('click', () => {
            let algdata = this.app.loadModal(this.uploadalgdata, { adv: true }, { toolList: this.toolList ,type:'project'})
            algdata.event._addEvent('uploadalgdata.upload', (value) => {
                let filePath = ES.selctorDoc("#file1").val();
                let fileType = this.getFileType(filePath);
                if ("csv" != fileType && 'xlsx' !== fileType && 'xls' !== fileType) {
                    ES.selctorDoc("#filechoose").val("");
                    this.app.alert.show({
                        title: '',
                        template: '<span style="font-size: 18px;margin-left:20px;">格式错误，上传失败。</span>',
                        close: false,
                        sure: () => {
                            this.app.alert.hide();
                        }
                    })
                } else {
                    $.ajaxFileUpload({
                        url: '/aaa/v1/alg/sar/import', // that.app.domain+'/ccc/user/import',
                        secureuri: false,
                        dataType: "JSON",
                        async: false,
                        data: {
                            projectId: this.app.parpam['projectid'],
                            type: value.data.type,
                            accessToken: window.localStorage.accessToken,
                            transferNii: value.data.transferNii ? 1 : 0,
                            toolType: value.data.toolType ? value.data.toolType.split(',')[1] : '',
                            toolId: value.data.toolType ? value.data.toolType.split(',')[0] : '',
                          dataType: value.data.dataType ? value.data.dataType : '',
                        },
                        type: 'post',
                        fileElementId: 'file1',
                        success: (data, status, e) => {
                            let jsonArr = JSON.parse(data.match(/{.+}/g)[0])
                            algdata.close();
                            if (jsonArr.code == 0) {
                                this.listInfo()
                                this.app.alert.show({
                                    title: '',
                                    template: '<span style="font-size: 18px;margin-left:20px;">成功导入' + jsonArr.data.successCount + '条，失败' + jsonArr.data.errorCount + '条</span>',
                                    sure: false,
                                    close: true,
                                    footer: true
                                })
                                this.event._dispatch('projectdata.datachange')
                            } else {
                                let msg = jsonArr.code == -1 ? '繁忙' : jsonArr.msg
                                this.app.alert.show({
                                    title: '',
                                    template: '<span style="font-size: 18px;margin-left:20px;">' + msg + '</span>',
                                    sure: false,
                                    close: true,
                                    footer: true
                                })
                            }
                            this.close()
                            this.dom.find('.file').remove();
                            this.dom.find('.btnarea').append('<input class="file" type="file" id="file" name="file"/>')
                            // that.bindchangefile();
                        }
                    });
                }
            })
        })
        this.dom.find('.viewsfdata').on('click', () => {
            this.queryImportResult()
        })
        this.dom.find('.propreview').on('click', async () => {
            let json = {
                projectId: this.projectId
            }
            this.app.loading.show()
            let res = await this.api.project_anno_read(json)
            this.app.loading.hide()
            if (res.code == 0) {
                if (res.data.annotationItemList.length == 0 && res.data.imageAnnotationList.length == 0) {
                    this.app.alert.show({
                        title: ' ',
                        msg: '请先设置影像标注信息后预览试用',
                        close: true,
                        footer: true
                    })
                } else {
                    this.bigPic = res.data.largeFigure
                    this.series_count(true);
                }
            }
        })
        this.dom.find('.upload').on('click', () => {
            this.dom.find('.file').click();
        })
        this.dom.find('.downtemplete').on('click', () => {
            this.app.loading.show()
            setTimeout(() => {
                this.app.loading.hide()
            }, 1000)
            var a = document.createElement("a");
            var url = '/images/page/s.csv';
            a.href = url;
            a.download = '模板.csv';
            a.click();
        })
      this.dom.find('.copytask').on('click',async ()=>{
        let taskIdList = []
        if(this.dom.find('.list-content .choose').dom){
          this.dom.find('.list-content .choose').dom.forEach((val)=>{
            console.log(val.parent().parent())
            taskIdList.push(val.parent().parent().attr('nowid'))
          })
          this.app.loading.show()
          let res = await this.api.task_cloneList({projectId:this.projectId,taskIdList:taskIdList})
          this.app.loading.hide()
          let msg= ''
          if(res.code == 0){
            msg = '复制成功'
            this.dom.find('.yyytasklist .choose').removeClass('choose')
          }else{
            msg = '复制失败'
          }
          this.app.alert.show({
            title: '',
            template: '<span style="font-size: 18px">'+msg+'</span>',
            close: false,
            sure: () => {
            }
          })
        }else{
          this.app.alert.show({
            title: '',
            template: '<span style="font-size: 18px;margin-left:20px;">请先选择需要复制的任务</span>',
            close: false,
            sure: () => {
            }
          })
        }
      })
        this.dom.find('.isgetalgres .radio-box').on('click', function() {
            let dom = ES.selctorDoc(this)
            if (dom.hasClass('choose')) {
                dom.removeClass('choose')
                that.dom.find('.toolChoose').addClass('hide')
                that.dom.find('.toolChoose .choose').removeClass('choose')
            } else {
                dom.addClass('choose')
                that.dom.find('.toolChoose').removeClass('hide')
            }
        })
        this.dom.find('.isgetalgres .check-box').on('click', async function() {
            let algResultType = ''
            let dom = ES.selctorDoc(this)
            if (dom.hasClass('choose')) {
                dom.removeClass('choose')
            } else {
                dom.addClass('choose')
            }
            that.dom.find('.toolChoose .choose').dom.forEach((val) => {
                algResultType += val.parent().attr('data-id') + ','
            })
            let res = await that.api.project_edit_algResultType({ projectId: that.projectId, algResultType: algResultType })
            if (res.code == 0) {

            } else {
                alert('更新失败')
            }
        })
        this.binduploadevent()
        this.project_basic_read()
    }

    render() {
        this.series_count();
    }

    async gettask(value) {
        let json = {
            projectId: this.projectId,
            taskNameKey: value,
            type: 1
        }
        this.prodatapoll.inputarea.loading(true)
        let res = await this.api.task_like_query(json)
        this.prodatapoll.inputarea.loading(false)
        if (res.code == 0) {
            this.prodatapoll.tasklist(res.data.list)
        }
    }

    binduploadevent() {
        this.dom.find('.file').on('change', () => {
            let filePath = ES.selctorDoc("#file").val();
            let fileType = this.getFileType(filePath);
            if ("csv" != fileType) {
                ES.selctorDoc("#filechoose").val("");
                this.app.alert.show({
                    title: '',
                    template: '<span style="font-size: 18px;margin-left:20px;">格式错误，上传失败。</span>',
                    close: false,
                    sure: () => {
                        this.app.alert.hide();
                        this.dom.find('.file').remove();
                        this.dom.find('.imagedata').append('<input class="file" type="file" id="file" name="file"/>')
                        this.binduploadevent();
                    }
                })
            } else {
                this.app.loading.show()
                $.ajaxFileUpload({
                    url: '/aaa/v1/project/series/import', // that.app.domain+'/ccc/user/import',
                    secureuri: false,
                    dataType: "JSON",
                    timeout: 60000,
                    async: false,
                    data: {
                        projectId: this.projectId,
                        accessToken: window.localStorage.accessToken
                    },
                    type: 'post',
                    fileElementId: 'file',
                    sequentialUploads: true,
                    beforeSend: function(xhr, data) {
                        xhr.setRequestHeader('accessToken', window.localStorage.accessToken);
                    },
                    success: (data, status, e) => {
                        console.log(data, 'data', status, e)
                        console.log(data.match(/{.+}/g))
                        let jsonArr = JSON.parse(data.match(/{.+}/g)[0])
                        this.app.loading.hide()
                        if (jsonArr.code == 0) {
                            console.log('success')
                            this.app.alert.show({
                                title: '',
                                template: '<span style="font-size: 18px;margin-left:20px;">成功导入' + jsonArr.data.successNumber + '条，' + jsonArr.data.failNumber + '条不在影像数据中或未被识别或重复</span>',
                                sure: false,
                                close: function() {

                                },
                                footer: true
                            })
                        } else {
                            this.app.alert.show({
                                title: '',
                                template: '<span style="font-size: 18px;margin-left:20px;">上传失败</span>',
                                sure: false,
                                close: true,
                                footer: true
                            })
                        }
                        this.dom.find('.file').remove();
                        this.dom.find('.imagedata').append('<input class="file" type="file" id="file" name="file"/>')
                        this.binduploadevent();
                        this.getseries();
                    }
                });

            }

        })
    }

  async heartSegment_resultRead(){
    let res = await this.api.heartSegment_resultRead({projectId:this.projectId})
    let algType = null
    if(res.code == 0 ){
      if(res.data.algType == 1){
        algType = 'isgetalgres'
      }else if(res.data.algType == 2){
        algType = 'copd'
      }else if(res.data.algType == 3){
        algType = 'fjj'
      }
      if(this.type == 'edit'){
        if(algType){
          this.dom.find('.'+algType+' .startalg').removeClass('hide')
        }else{
          this.dom.find('.startalg').removeClass('hide')
        }
      }
      if(algType){
        this.dom.find('.'+algType).append(`<p>成功数量：${res.data.successCount},失败数量：${res.data.failCount},处理中数量：${res.data.processCount},未开始数量：${res.data.notStartCount}</p>`)
      }
      let temp = ''
      if(algType){
        temp = algType+' .startalg'
      }else{
        temp = 'startalg'
      }
      console.log(temp,'temp')
      this.dom.find('.'+temp).on('click', async (e) => {
        this.app.alert.show({
          title: ' ',
          msg: '确认开启吗？',
          close: true,
          sure:()=>{
            let config = [{
              name: 'taskType',
              type: 'radio',
              title: '请选择类型',
              'check': 'empty',
              remark: '',
              data: [{val: '强制重跑', idx: '3'}, {val: '处理失败', idx: '2'}, {val: '新增序列', idx: '1'}]
            }]
            this.editconclusion = this.app.loadModal(this.formsubmit, {adv: true}, {title: '选择类型', config: config})
            this.editconclusion.event._addEvent('formsubmit.submit',async (data) => {
                let msg = ''
                this.app.loading.show()
                let resjson = await this.api.project_joinAlg({projectId:this.projectId,joinAlg:true})
                this.app.loading.hide()
                if (resjson.code == 0) {
                    this.app.loading.show()
                    let res = await this.api.heartSegment_start({projectId:this.projectId,taskType:data.taskType,algType:ES.selctorDoc(e.target).attr('algType') })
                    this.app.loading.hide()
                    if(res.code == 0){
                        msg = '开启成功'
                        this.dom.find('.startalg').addClass('hide')
                        ES.selctorDoc(e.target).parent().find('p').remove()
                        ES.selctorDoc(e.target).parent().append('<p>结果正在处理中</p>')
                    }else{
                        msg = res.msg
                    }
                    this.editconclusion.close()
                }else {
                   msg = resjson.msg
                }
              this.app.alert.show({
                title: ' ',
                msg: msg,
                close: true,
                footer: true
              })
            })
          }
        })
      })
    }
  }

  async project_basic_read() {
        let res = await this.api.project_basic_read({ id: this.projectId })
        if (res.code == 0) {
            let toolArr = []
            toolArr = res.data.algResultType.split(',')
            if (toolArr.length > 0) {
                this.dom.find('.isgetalgres .radio-box').click()
                toolArr.forEach((val) => {
                    this.dom.find('.toolChoose p[data-id="' + val + '"').find('.check-box').addClass('choose')
                })
            }
        }
    }

    getFileType(filePath) {
        var startIndex = filePath.lastIndexOf(".");
        if (startIndex != -1)
            return filePath.substring(startIndex + 1, filePath.length).toLowerCase();
        else return "";
    }

    download(value) {
        let sting1 = encodeURI(JSON.stringify(this.apidata))
        let url = this.app.domain1 + 'v1/project/series/export?projectId=' + this.projectId + '&operation=' + value + '';
        Tool.downfile(url, this.app)
    }
    //清空序列
    async clearandnew() {
        let json = {
            projectId: this.projectId
        }
        this.app.loading.show()
        let res = await this.api.clearseries(json)
        this.app.loading.hide()
        if (res.code == 0) {
            this.addimage(1)
            this.addmodel.close()
            this.addmodel = null;
        }
    }
    //添加序列
    async addimage(value) {
        if (this.model.getData('listInfo').list.length > 0) {
            this.dom.find('.viewimage').removeClass('hide')
            // that.model.getData('listInfo').list.forEach(function (val,idx) {
            //     that.apiData.seriesInstanceUidList.push(val.seriesInstanceUID);
            // })
            this.apiData.operation = value
            this.app.loading.show()
            let res = await this.api.updateproimage(this.apiData)
            this.app.loading.hide()
            if (res.code == 0) {
                this.addmodel.close()
                this.addmodel = null;
            }
        } else {
            //this.dom.find('.viewimage').addClass('hide')
            this.getseries();
        }
    }
    //获取医院列表
    async gethospital(value, value2) {
        if (this.prodatapoll && this.prodatapoll.yymc) {
            this.prodatapoll.yymc.loading(true)
        }
        if (this.addmodel && this.addmodel.duoxuanobj && this.addmodel.duoxuanobj.hospitalCode) {
            this.addmodel.duoxuanobj.hospitalCode.loading(true)
        }
        let json1 = { service: 'DR', method: '/v1/hospital/search', params: JSON.stringify({ code: value }) }
        let res = await this.api.gethospital(json1)
        if (this.prodatapoll && this.prodatapoll.yymc) {
            this.prodatapoll.yymc.loading(false)
        }
        if (this.addmodel && this.addmodel.duoxuanobj && this.addmodel.duoxuanobj.hospitalCode) {
            this.addmodel.duoxuanobj.hospitalCode.loading(false)
        }
        if (res.code == 0) {
            let list = []
            res.data.list.forEach(function(val, idx) {
                let obj = {
                    idx: val.code,
                    val: val.code
                }
                list.push(obj)
            })
            this.model.setData('hospitalInfo', list)
            if (this.prodatapoll) {
                this.prodatapoll.resetxlk(list)
            }
            if (value2) {
                this.model.setData('totalInfo', res.data)
            }
        }
    }
    //查询可添加的序列
    async getlist(value, value2) {
        this.app.loading.show()
        let json = { service: 'DR', method: '/v1/series/search', params: JSON.stringify(value) }
        let res = await this.api.getimage(json)
        this.app.loading.hide()
        if (res.code == 0) {
            if (value2) {
                this.model.listInfo = res.data
                this.model.setData('totalInfo', res.data)
            } else {
                this.model.setData('listInfo', res.data)
            }
        }
    }
    //查询项目下配置的所有工具
    async alg_sar_list_tool() {
        let json = {
            projectId: this.projectId
        }
        this.toolList = []
        let res = await this.api.alg_sar_list_tool(json)
        if (res.code == 0) {
            res.data.forEach((val) => {
                this.toolList.push({ val: val.type, idx: val.id + ',' + val.type })
            })
        }
    }
    //查询已导入算法数据
    async queryImportResult() {
        let res = await this.api.get_import_result_list_search({ projectId: this.projectId, page: 1, pageSize: 10 })
        let alglist = this.app.loadModal(this.alglist, { adv: true }, { data: res.data })
        alglist.event._addEvent('alglist.listaction', async (value) => {
            console.log(value)
            switch (value.classname) {
                case 'delete':
                    break;
                case 'viewfail':
                    let param = encodeURI(JSON.stringify({ resourceId: value.id.split(',')[1] }))
                    this.api.HttpRequest.downLoadFile(`${this.app.domain1}v1/base/fileResource/download?param=${param}&&accessToken=${window.localStorage.accessToken}`)
                    break;
            }
        })
        alglist.event._addEvent('alglist.pagenumber', async (value) => {
            console.log(value, 'page')
            let res = await this.api.get_import_result_list_search({ projectId: this.projectId, page: value, pageSize: 10 })
            alglist.setList(res.data, false)
        })
    }
    //渲染已引用该项目的任务列表头部
    setTitle() {
        let obj = {};
        let obj1 = {}
        obj['icon'] = {
            "taskName": {
                name: '<span data-i18n="age" data-name="年龄">任务名称</span>',
                type: 'text',
                code: 'checkid',
                w: '20%',
                ww: '20%',
                n: "40"
            },
            "startTime": {
                name: '<span data-i18n="age" data-name="年龄">任务发布时间</span>',
                type: 'text',
                code: 'pid',
                w: '20%',
                ww: '20%',
            },
            "endTime": {
                name: '<span data-i18n="age" data-name="年龄">任务到期时间</span>',
                type: 'text',
                code: 'pname',
                w: '20%',
                ww: '20%'
            },
            "studyTotalCount": {
                name: '<span data-i18n="age" data-name="年龄">样本数量</span>',
                type: 'text',
                code: 'psex',
                w: '20%',
                ww: '20%'
            },
            "vendors": {
                name: '<span data-i18n="age" data-name="年龄">派发公司</span>',
                type: 'text',
                code: 'age',
                w: '20%',
                ww: '20%'
            },
        };
        obj['type'] = 'center';
        obj['chose'] = 'all';
        obj['chosew'] = '30px';
        obj['initPagina'] = false;
        obj['pagesizeSet'] = false;
        obj['tablewidth'] = ES.selctorDoc('.yyytasklist').box().clientWidth - 40;
        require.ensure("../moduleslibs/table/table", () => {
            let cont_table = require("../moduleslibs/table/table")
            this.table = this.app.loadModule(cont_table, this.dom.find('.yyytasklist'), {
                id: 'yyytasklist',
                header: obj
            });
            this.table.event._addEvent('table.pagenumber', (value) => {
                this.viewData.page = parseInt(value);
                this.table.changenum(this.viewData.page);
                this.setMain();
            });
            this.table.event._addEvent('table.pagesize', (value) => {
                this.viewData.pageSize = value.num;
                this.viewData.page = 1;
                this.setMain(true);
            });
            this.dom.find('.list-content').removeClass('hide');
            this.viewData.page = 1;
            this.viewData.projectIdList = [];
            this.viewData.projectIdList.push(parseInt(this.projectId));
            this.viewData.pagesize = 10;
            this.setMain(true);
        })
    }
    //渲染已引用该项目的任务列表
  async setMain(bool) {
    let data2 = []
    this.table.showloading()
    let res = await this.api.task_stayClone_search({projectId:this.projectId})
    if (res.code == 0) {
      if (res.data.length > 0) {
        res.data.forEach((val, idx) => {
          let obj = {}
          obj.id = val.taskId;
          val.id = val.taskId;
          val.vendors = ''
          if (val.venderNameList.length&&val.venderNameList.length>0) {
            val.venderNameList.forEach(function(v, i) {
              val.vendors += v
              if (i != val.venderNameList.length - 1) {
                val.vendors += '、'
              }
            })
          }
          data2.push(obj)
          val.startTime = Tool.time(val.startTime, 'yyyy-mm-dd')
          val.endTime = Tool.time(val.endTime, 'yyyy-mm-dd')
        })
        this.table.setData(res.data, data2)
      } else {
        this.table.noData();
        this.dom.find('.copytask').addClass('hide')
      }
    }
    if (bool) {
      this.table.getTotal(res.data.pages, 2, res.data.total)
    }
    this.resize()
    this.initscroll();
  }
    //获取序列列表
    async getseries(value) {
        if (value) {
            value.projectId = this.projectId
        } else {
            value = {
                projectId: this.projectId,
                page: 1,
                pageSize: 10
            }
        }
        this.app.loading.show()
        let res = await this.api.getproimage(value)
        this.app.loading.hide()
        if (res.code == 0) {
            this.projectseries = res.data.list;
            if (res.data.list && res.data.list.length > 0) {
                this.dom.find('.viewimage').removeClass('hide')
            }
            this.prodatapoll.setMain(res, this.resetPage)
        }
    }
    //查询该项目下序列数
    async series_count(change) {
        this.app.loading.show()
        let res = await this.api.series_count({ projectId: this.projectId })
        this.app.loading.hide()
        if (res.data.count > 0) {
            this.dom.find('.viewimage').removeClass('hide')
        } else {
            this.dom.find('.viewimage').addClass('hide')
        }
        if (change) {
            this.model.setData('count', res.data.count)
        }
    }
    //项目预览时判断是否有序列
    count() {
        if (this.model.count > 0) {
            let page = this.bigPic ? 'drapCanvasView' : 'markpreview'
            window.location.href = 'http://' + window.location.host + '/#!/' + page + '/1/' + this.projectId + '/preview/' + this.app.parpam.status + '/' + this.projectType
            window.location.reload()
        } else {
            this.app.alert.show({
                title: ' ',
                msg: '项目下不存在影像序列，请先添加',
                close: true,
                footer: true
            })
        }
    }
    async get_last_import_log(){
      // let res = await this.api.sar_get_last_import_log({projectId: this.projectId})
      // if(res.code == 0){
      //   switch(res.data.status){
      //     case 1:
      //       this.dom.find('.addsfdata').addClass('hide')
      //       break;
      //       case 2
      //   }
      // }
    }
    resize() {
        let ch = ES.selctorDoc(window).box().clientHeight - 100
        let cw = ES.selctorDoc(window).box().clientWidth - 40
        ES.selctorDoc('.createprothree').css({ 'height': ch, 'width': cw });
        // ES.selctorDoc('.yyytasklist .maintable').css({ 'height': ch - 350 });
    }

    initscroll() {
        if (this.myScroll) {
            this.myScroll.refresh()
            return
        }
        var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
        ES.selctorDoc('.createprothree').attr('id', rid)
        this.myScroll = new IScroll('#' + rid, {
            scrollbars: true,
            mouseWheel: true,
            interactiveScrollbars: true,
            hideScrollbar: false,
            vScrollbar: true,
            shrinkScrollbars: 'scale',
            fadeScrollbars: false,
            disableMouse: true,
            disablePointer: true
        });
    }
}

module.exports = createprothree;



